.App {
  width: 100%;
  font-family: Lato;
}

section {
  display: grid;
  gap: 20px;
  padding-inline-start: 28px;
  padding-inline-end: 28px;
  padding-top: 80px;
  padding-bottom: 25px;

  @media (min-width: 601px) {
    padding: 70px 50px 70px 50px;
    align-items: center;
  }
}

p {
  color: #57575C;
  font-family: Lato;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  margin: 20px 0;
}

