  color: #007bc2;
  font-family: Lato;
  font-size: 36px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
} */

h2 {
  color: #032559;
  font-family: Lato;
  font-size: 24px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

h3 {
  color: #1a1b1f;
  font-family: Lato;
  font-style: normal;
  margin: 0px 0 0 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 34px;
}

h4 {
  font-family: Lato;
  color: rgb(26, 27, 31);
  font-size: 1rem;
  font-weight: 400;
  line-height: 28px;
  margin: 10px 0 0 0;
}

h5 {
  font-family: Lato;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: lowercase;
  margin-top: 5px;
  color: #57575C;
}
